  .transition-transform,
  .transition-colors {
    transition-duration: 200ms;
  }

  /* Better touch targets - minimum 44x44px */
  button, a, input, select, textarea {
    touch-action: manipulation;
    min-height: 44px;
  }

  /* Mobile-friendly tables */
  table {
    display: block;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    white-space: nowrap;
  }

  /* Prevent horizontal overflow */
  body, html {
    overflow-x: hidden;
    max-width: 100vw;
  }

  /* Mobile-friendly spacing */
  .container {
    padding-left: 1rem;
    padding-right: 1rem;
  }

  /* Better card layouts on mobile */
  .card, .nsc-dashboard-card, .nsc-bg-card {
    border-radius: 0.75rem;
    margin-bottom: 1rem;
  }

  /* Mobile-optimized text sizes for better readability */
  h1 {
    font-size: 1.875rem;
    line-height: 1.2;
  }

  h2 {
    font-size: 1.5rem;
    line-height: 1.3;
  }

  h3 {
    font-size: 1.25rem;
    line-height: 1.4;
  }

  /* Adjust sidebar width for mobile landscape */
  aside {
    max-width: 80vw;
  }

  /* Improve button spacing on mobile */
  .btn, button {
    min-height: 44px;
  }

  /* Full width inputs on mobile */
  input:not([type="radio"]):not([type="checkbox"]),
  select,
  textarea {
    width: 100%;
  }
}
